.header {
    background-color: $verde;

    .contenido-header {
        padding: $separacion / 2 0;
        
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

    }

    h1 {
        color: $blanco;
        margin-bottom: 1rem;
        text-align: center;
        @include tablet {
            margin-bottom: 0;
        }
    }

    &.fijo {
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .1rem #4444;
    }
}

.navegacion-principal {

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;

    a {
        color: $blanco;
        font-size: 2rem;
        text-align: center;

        &:hover {
            color: $amarillo;
        }
    }


}