@mixin telefono {
    @media (min-width: $movil) {
        @content; // pasa las propiedades que se pasen en el mixin
    }
}

@mixin tablet {
    @media (min-width: $tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}

@mixin grid($columnas) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
}