.informacion {
   padding: $separacion 0;

    @include tablet {
        @include grid(2);
        column-gap: $separacion;
    }

    img {
        border-radius: 1rem;
    }
}

.informacion__texto {
    display: flex;
    flex-direction: column;
    justify-content: center;

    .fecha {
        color: $rosa;
        font-weight: $bold;
        margin-top: 5rem;
    }

    .texto {
        line-height: 1.5;
    }
}