.galeria {
    padding: $separacion 0;

    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    padding: 0;
    list-style: none;

    @include grid(2);

    @include tablet {
        @include grid(4);
    }

    @include desktop {
        @include grid(6);
    }

    li {
        padding: $separacion / 3;   
    }

    img {
        border-radius: 1rem;
        cursor: pointer;
    }
}

.overlay {
    position: fixed;
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;

    img {
        width: 60%;
        border-radius: 1rem;
    }
}

.btn-cerrar {
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $black;
    font-size: 3rem;
    border-radius: 50%;

    &:hover {
        cursor: pointer;
    }
}